	var artid,_self
export default {
	data() {
		return {
			article:[],  //文章基础信息
			artContents:[],   //文章项目
			graceSkeleton:'ing'
		}
	},
	methods: {
		showImgs : function(e){
					var currentUrl = e.currentTarget.dataset.url;
					var imgsNeedShow = [];
					for(var i = 0; i < this.artContents.length; i++){
						if(this.artContents[i].type == 'image'){
							imgsNeedShow.push(this.artContents[i].content);
						}
					}
					uni.previewImage({
						urls    :imgsNeedShow,
						current :currentUrl
					});
				}
	},
	onLoad(option) {
		_self=this
		artid=option.artid;
		//加载文章详情
		uni.showLoading({title:'加载中'})
		uni.request({
			url:_self.apiServer+'art&m=infoWithUser&artid='+artid,
			method:'GET',
			data:{},
			success: res => {
				var art=res.data.data
				// 将文章内容转换成数组
				var artContentItems = JSON.parse(art.art_content);
				// 首先规划骨架
				this.artContents = []; 
				 for(var i = 0; i < artContentItems.length; i++){
					this.artContents.push({'type': artContentItems[i].type});
					}
					setTimeout(function(){
										_self.article        = art;
										_self.artContents   = artContentItems;
										_self.graceSkeleton = 'end';
										uni.hideLoading();
									},500);
			}
		})
	}
}
